.sib-loader{
  display:flex;
  z-index:var(--sib-z-index_loader,1099);
}
  .sib-loader svg{
    animation:var(--sib-animation_indicator-spin, indicator-spin 1.3s cubic-bezier(.46, .35, .39, .85) infinite);
  }

.sib-loader--absolute{
  left:50%;
  position:absolute;
  top:50%;
  transform:translate(-50%, -50%);
}
