@keyframes indicator-spin{
  0%{
    transform:rotate(0deg);
  }
  100%{
    transform:rotate(360deg);
  }
}

@keyframes sweep{
  0%{
    max-height:var(--details-content-height-closed, 0);
    opacity:0;
  }
  100%{
    max-height:var(--details-content-height-open, auto);
    opacity:1;
  }
}

@keyframes fade-in{
  0%{
    opacity:0;
  }
  100%{
    opacity:1;
  }
}

@keyframes progressBar{
  from{
    width:100%;
  }
  to{
    width:0%;
  }
}

@keyframes skeleton-loading{
  0%{
    background-position:-468px 0;
  }
  100%{
    background-position:468px 0;
  }
}

@keyframes slide-right{
  from{
    margin-left:-100%;
  }
  to{
    margin-left:0%;
  }
}

@keyframes slide-left{
  from{
    margin-left:100%;
  }
  to{
    margin-left:0%;
  }
}

@keyframes pulse{
  0%{
    box-shadow:0 0 0 0 hsla(206, 100%, 39%, .4);
  }
  100%{
    box-shadow:0 0 0 2px hsla(206, 100%, 39%, .4);
  }
}

@keyframes slide-modal-to-top{
  from{
    bottom:-600px;
    opacity:0;
  }
  to{
    bottom:0;
    opacity:1;
  }
}

@keyframes slide-modal-to-bottom{
  from{
    bottom:0;
    opacity:1;
  }
  to{
    bottom:-600px;
    opacity:0;
  }
}

@keyframes fade-in-modal{
  from{
    opacity:0;
    transform:scale(.8);
  }
  to{
    opacity:1;
    transform:scale(1);
  }
}

@keyframes fade-out-modal{
  from{
    opacity:1;
    transform:scale(1);
  }
  to{
    opacity:0;
    transform:scale(.8);
  }
}

@keyframes backdrop-open{
  from{
    opacity:0;
    visibility:hidden;
  }
  to{
    visibility:visible;
  }
}

@keyframes backdrop-close{
  from{
    visibility:visible;
  }
  to{
    opacity:0;
    visibility:hidden;
  }
}

@keyframes slide-left-with-bounce{
  0%{
    opacity:0;
    transform:translateX(-232px);
  }
  60%{
    opacity:1;
    transform:translateX(8px);
  }
  100%{
    transform:translateX(0);
  }
}

@keyframes slide-right-with-bounce{
  0%{
    opacity:0;
    transform:translateX(232px);
  }
  60%{
    opacity:1;
    transform:translateX(-8px);
  }
  100%{
    transform:translateX(0);
  }
}

@keyframes slide-nav-from-right{
  0%{
    width:100px;
  }
  60%{
    width:61px;
  }
  100%{
    width:66px;
  }
}

@keyframes slide-nav-from-left{
  0%{
    width:75px;
  }
  60%{
    width:239px;
  }
  100%{
    width:234px;
  }
}

@keyframes progress-bar-indicator{
  0%{
    width:0;
  }
  100%{
    width:100%;
  }
}
