.sib-btn--loader{
    opacity:.5;
  }

.sib-btn--loader .sib-loader svg{
        margin-left:0;
        margin-right:0;
      }

.sib-btn--loader>*:not(.sib-loader){
    opacity:0;
  }

.sib-btn--full-width{
    justify-content:center;
    width:100%;
  }
